import emailjs from '@emailjs/browser';

//  CONFIGURATION EMAILJS - Mme configuration que ContactForm
const EMAILJS_CONFIG = {
  serviceId: 'service_go62bxn',        // Service ID Gmail configur
  templateId: 'template_sirltvl',      // Template ID configur ( crer un nouveau template pour les simulations)
  publicKey: 'gBCd9v4gii2QckAgK'      // Cl publique configure
};

/**
 * Envoie un email avec les rsultats de simulation via EmailJS
 * Utilise la mme configuration que le formulaire de contact
 * @param {string} recipientEmail - Email du destinataire
 * @param {Object} simulationData - Donnes de simulation  envoyer
 * @returns {Promise} - Promise qui se rsout quand l'email est envoy
 */
export const sendSimulationEmail = async (recipientEmail, simulationData) => {
  // Formater les donnes pour le template email
  const templateParams = {
    // Paramtres destinataire (comme dans ContactForm)
    to_email: recipientEmail,
    to_name: 'Utilisateur RetraiteClair',
    
    // Paramtres expditeur (pour que l'email arrive de retraiteClair@gmail.com)
    from_email: 'retraiteClair@gmail.com',
    from_name: 'RetraiteClair',
    reply_to: 'retraiteClair@gmail.com',
    
    // Sujet de l'email
    subject: 'Votre simulation Retraite Progressive - RetraiteClair',
    
    // Message principal avec toutes les donnes de simulation
    message: `Voici votre simulation de retraite progressive gnre le ${new Date().toLocaleDateString('fr-FR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })}

 VOS RSULTATS :

 Revenu total net mensuel : ${formatCurrency(simulationData.revenusNets?.total || 0)}
 Salaire temps partiel : ${formatCurrency(simulationData.revenusNets?.tempsPartiel || 0)}
 Pension retraite : ${formatCurrency(simulationData.revenusNets?.pension || 0)}
 Temps partiel : ${simulationData.details?.tempsPartiel || simulationData.tempsPartiel || 80}%
 conomie fiscale annuelle : ${formatCurrency(simulationData.impactFiscal?.economieAnnuelle ?? (simulationData.impactFiscal?.economie ? simulationData.impactFiscal.economie * 12 : 0))}

Pour plus de dtails et pour modifier vos paramtres, visitez https://retraiteclair.com

Cordialement,
L'quipe RetraiteClair`,
    
    // Date formate
    date: new Date().toLocaleString('fr-FR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  };

  console.log('Paramtres EmailJS (simulation):', templateParams);

  try {
    // Envoi via EmailJS - Mme structure que ContactForm
    const response = await emailjs.send(
      EMAILJS_CONFIG.serviceId,
      EMAILJS_CONFIG.templateId,
      templateParams,
      EMAILJS_CONFIG.publicKey
    );

    console.log('Email de simulation envoy avec succs:', response);
    return response;
  } catch (error) {
    console.error('Erreur lors de l\'envoi de l\'email de simulation:', error);
    
    // Gestion d'erreur similaire  ContactForm
    let errorMessage = 'Une erreur est survenue lors de l\'envoi de l\'email.';
    
    if (error.status === 400 && error.text && error.text.includes('service ID not found')) {
      errorMessage = 'Service EmailJS introuvable. Vrifiez que le service est actif sur https://dashboard.emailjs.com/admin';
    } else if (error.status === 422) {
      errorMessage = 'Erreur de configuration EmailJS : L\'adresse de destination est vide. Vrifiez la configuration du template.';
    } else if (error.message && error.message.includes('400')) {
      errorMessage = 'Configuration EmailJS manquante. Veuillez vrifier la configuration.';
    } else if (error.message && error.message.includes('Invalid')) {
      errorMessage = 'Configuration EmailJS invalide. Vrifiez vos cls de service.';
    } else if (error.text) {
      errorMessage = `Erreur EmailJS: ${error.text}`;
    }
    
    throw new Error(errorMessage);
  }
};

/**
 * Formatage d'un montant en devise
 */
const formatCurrency = (amount) => {
  return new Intl.NumberFormat('fr-FR', {
    style: 'currency',
    currency: 'EUR',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0
  }).format(amount);
};

