import React from 'react';
import SEOHead from '../components/SEOHead';
import PageHeader from '../components/PageHeader';
import LegalPageLayout from '../components/LegalPageLayout';

const PolitiqueConfidentialite = () => {
  return (
    <LegalPageLayout>
      <SEOHead 
        title="Politique de confidentialit - RetraiteClair"
        description="Politique de confidentialit de RetraiteClair - Comment nous collectons, utilisons et protgeons vos donnes personnelles."
        keywords="politique confidentialit, protection donnes, RGPD, retraiteclair"
        canonical="/politique-confidentialite"
      />
      
      <PageHeader 
        title="Politique de confidentialit"
        subtitle="Protection de vos donnes personnelles"
        backgroundImage=""
      />
      
      <div className="legal-content">
        <div className="container">
          <div className="legal-section">
            <h2>1. Collecte des donnes</h2>
            <p>
              RetraiteClair collecte des donnes personnelles uniquement dans les cas suivants :
            </p>
            <ul>
              <li><strong>Formulaire de contact :</strong> nom, email, message</li>
              <li><strong>Simulateur :</strong> donnes de calcul (non stockes de manire permanente)</li>
              <li><strong>Cookies et localStorage :</strong> voir section dtaille ci-dessous</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>2. Cookies et technologies de stockage local</h2>
            <p>
              Le site RetraiteClair utilise les technologies suivantes pour amliorer votre exprience :
            </p>
            
            <h3>2.1. Cookies</h3>
            <p><strong>Cookies essentiels (ncessaires au fonctionnement) :</strong></p>
            <ul>
              <li><strong>Aucun cookie essentiel</strong> n'est utilis actuellement</li>
            </ul>
            
            <p><strong>Cookies ncessitant votre consentement :</strong></p>
            <ul>
              <li><strong>retraiteClair_cookieConsent :</strong> mmorisation de votre choix de consentement aux cookies (accept/refus) - dure : 13 mois</li>
              <li><strong>retraiteClair_cookieConsentDate :</strong> date de votre consentement - dure : 13 mois</li>
              <li><strong>consent-given :</strong> mmorisation de votre consentement (true/false) - dure : 13 mois</li>
              <li><strong>consent-date :</strong> date de votre consentement - dure : 13 mois</li>
              <li><strong>Cookies Google Analytics (_ga, _gid, _gat) :</strong> analyse de l'utilisation du site, mesure d'audience - dure : 13 mois (ncessitent votre consentement)</li>
              <li><strong>Cookies Meta Pixel (fbq) :</strong> suivi des conversions et mesure de performance publicitaire - dure : 90 jours (ncessitent votre consentement)</li>
            </ul>

            <h3>2.2. localStorage</h3>
            <p>Les donnes suivantes sont stockes localement sur votre appareil :</p>
            <ul>
              <li><strong>retraiteClair_personalInfo :</strong> sauvegarde temporaire des donnes du formulaire de simulation (salaire, ge, trimestres, etc.) pour faciliter la navigation entre les tapes - dure : jusqu' suppression manuelle ou expiration du navigateur</li>
              <li><strong>retraiteClair_data :</strong> sauvegarde temporaire des rsultats de simulation calculs - dure : jusqu' suppression manuelle ou expiration du navigateur</li>
              <li><strong>retraiteClair_cookieConsent :</strong> prfrence de consentement aux cookies - dure : 13 mois</li>
              <li><strong>retraiteClair_cookieConsentDate :</strong> date du consentement - dure : 13 mois</li>
              <li><strong>consent-given :</strong> statut du consentement (true/false) - dure : 13 mois</li>
              <li><strong>consent-date :</strong> date du consentement - dure : 13 mois</li>
            </ul>

            <h3>2.3. sessionStorage</h3>
            <p>Les donnes suivantes sont stockes uniquement pendant votre session de navigation :</p>
            <ul>
              <li><strong>simulatorScrollPosition :</strong> mmorisation de la position de dfilement dans le simulateur pour restaurer votre position lors de la navigation - dure : session uniquement (supprim  la fermeture de l'onglet)</li>
              <li><strong>blogArticleOrigin :</strong> mmorisation de l'origine de navigation vers un article de blog (depuis le simulateur ou autre) - dure : session uniquement (supprim  la fermeture de l'onglet)</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>3. Utilisation des donnes</h2>
            <p>
              Les donnes collectes sont utilises uniquement pour :
            </p>
            <ul>
              <li>Rpondre  vos demandes de contact</li>
              <li>Amliorer le fonctionnement du simulateur (sauvegarde temporaire des saisies)</li>
              <li>Analyser l'utilisation du site (donnes anonymises via Google Analytics, avec votre consentement)</li>
              <li>Mmoriser vos prfrences de navigation</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>4. Partage des donnes</h2>
            <p>
              RetraiteClair ne partage, ne vend ni ne loue vos donnes personnelles  des tiers. 
              Les donnes sont uniquement utilises en interne pour les finalits dcrites ci-dessus.
            </p>
            <p>
              <strong>Exceptions :</strong> 
            </p>
            <ul>
              <li>Les donnes analytiques anonymises peuvent tre transmises  <strong>Google Analytics</strong> (avec votre consentement) pour l'analyse de l'utilisation du site</li>
              <li>Les donnes de conversion peuvent tre transmises  <strong>Meta Pixel (Facebook)</strong> (avec votre consentement) pour mesurer l'efficacit des campagnes publicitaires</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>5. Conservation des donnes</h2>
            <p>
              Les donnes sont conserves pendant les dures suivantes :
            </p>
            <ul>
              <li><strong>Messages de contact :</strong> 3 ans maximum</li>
              <li><strong>Donnes de simulation (localStorage) :</strong> jusqu' suppression manuelle ou expiration du navigateur</li>
              <li><strong>Cookies de consentement :</strong> 13 mois maximum</li>
              <li><strong>Cookies Google Analytics :</strong> 13 mois maximum (avec consentement)</li>
              <li><strong>Cookies Meta Pixel :</strong> 90 jours maximum (avec consentement)</li>
              <li><strong>sessionStorage :</strong> dure de la session uniquement (supprim  la fermeture de l'onglet)</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>6. Vos droits (RGPD)</h2>
            <p>
              Conformment au Rglement Gnral sur la Protection des Donnes (RGPD), vous disposez des droits suivants :
            </p>
            <ul>
              <li><strong>Droit d'accs :</strong> connatre les donnes que nous dtenons sur vous</li>
              <li><strong>Droit de rectification :</strong> corriger des donnes inexactes</li>
              <li><strong>Droit d'effacement :</strong> supprimer vos donnes</li>
              <li><strong>Droit d'opposition :</strong> vous opposer au traitement de vos donnes</li>
              <li><strong>Droit  la portabilit :</strong> rcuprer vos donnes dans un format structur</li>
              <li><strong>Droit de limitation :</strong> limiter le traitement de vos donnes</li>
              <li><strong>Droit de retirer votre consentement :</strong>  tout moment pour les cookies non essentiels</li>
            </ul>
            <p>
              Pour exercer ces droits, contactez-nous  : <strong>contact@retraiteclair.fr</strong>
            </p>
          </div>

          <div className="legal-section">
            <h2>7. Contact DPO (Dlgu  la Protection des Donnes)</h2>
            <p>
              Pour toute question concernant le traitement de vos donnes personnelles, vous pouvez contacter :
            </p>
            <ul>
              <li><strong>Email :</strong> contact@retraiteclair.fr</li>
              <li><strong>Objet :</strong> "Demande DPO"</li>
            </ul>
            <p>
              Nous nous engageons  rpondre  votre demande dans un dlai d'un mois maximum.
            </p>
          </div>

          <div className="legal-section">
            <h2>8. Rclamer auprs de la CNIL</h2>
            <p>
              Si vous estimez que vos droits ne sont pas respects, vous avez le droit d'introduire une rclamation 
              auprs de la Commission Nationale de l'Informatique et des Liberts (CNIL) :
            </p>
            <ul>
              <li><strong>Site web :</strong> <a href="https://www.cnil.fr" target="_blank" rel="noopener noreferrer">https://www.cnil.fr</a></li>
              <li><strong>Adresse :</strong> 3 Place de Fontenoy - TSA 80715, 75334 Paris Cedex 07</li>
              <li><strong>Tlphone :</strong> 01 53 73 22 22</li>
            </ul>
          </div>

          <div className="legal-section">
            <h2>9. Scurit</h2>
            <p>
              RetraiteClair met en uvre des mesures techniques et organisationnelles appropries pour protger 
              vos donnes contre la perte, l'utilisation abusive, l'accs non autoris, la divulgation, 
              l'altration ou la destruction.
            </p>
            <p>
              Le site utilise HTTPS pour chiffrer les donnes en transit. Les donnes stockes localement 
              (localStorage, sessionStorage) restent sur votre appareil et ne sont pas transmises  nos serveurs.
            </p>
          </div>

          <div className="legal-section">
            <h2>10. Modifications</h2>
            <p>
              Cette politique peut tre modifie  tout moment. La version en vigueur est toujours disponible sur cette page.
            </p>
            <p><strong>Dernire mise  jour :</strong> {new Date().toLocaleDateString('fr-FR')}</p>
          </div>

          <div className="legal-section">
            <h2>11. Contact</h2>
            <p>
              Pour exercer vos droits ou pour toute question concernant cette politique :
            </p>
            <ul>
              <li><strong>Email :</strong> contact@retraiteclair.fr</li>
              <li><strong>Formulaire :</strong> <button 
                className="footer-legal-btn" 
                onClick={() => {
                  window.dispatchEvent(new CustomEvent('navigateToPage', { detail: { page: 'contact' } }));
                  setTimeout(() => {
                    window.dispatchEvent(new CustomEvent('backToHome'));
                    setTimeout(() => {
                      window.dispatchEvent(new CustomEvent('navigateToPage', { detail: { page: 'contact' } }));
                    }, 100);
                  }, 100);
                }}
              >
                Page de contact
              </button></li>
            </ul>
          </div>
        </div>
      </div>
    </LegalPageLayout>
  );
};

export default PolitiqueConfidentialite;


