import React, { useState } from 'react';
import { ChevronLeft, ChevronRight, CheckCircle, Clock, User, Briefcase, PiggyBank, Plus } from 'lucide-react';
import InputField from './InputField';
import InfoBox from './InfoBox';
import styles from './AvanceFormMultiStep.module.css';

const AvanceFormMultiStep = ({ onSubmit, isCalculating, sharedData = {}, onDataChange = () => {} }) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [formData, setFormData] = useState({
    salaireBrut: sharedData?.salaireBrut || '',
    tempsPartiel: sharedData?.tempsPartiel || '60',
    age: sharedData?.age || '',
    trimestres: sharedData?.trimestres || '',
    sam: sharedData?.sam || '',
    pensionComplete: sharedData?.pensionComplete || '',
    revenusComplementaires: sharedData?.revenusComplementaires || ''
  });

  const [errors, setErrors] = useState({});
  const [completedSteps, setCompletedSteps] = useState(new Set());

  // Dfinition des tapes avec des rfrences aux composants d'icnes
  const steps = [
    {
      id: 1,
      title: 'Votre situation actuelle',
      iconComponent: User,
      fields: ['age', 'salaireBrut']
    },
    {
      id: 2,
      title: 'Votre projet de retraite progressive',
      iconComponent: Clock,
      fields: ['tempsPartiel']
    },
    {
      id: 3,
      title: 'Carrire et retraite',
      iconComponent: Briefcase,
      fields: ['trimestres', 'sam']
    },
    {
      id: 4,
      title: 'Votre pension future',
      iconComponent: PiggyBank,
      fields: ['pensionComplete']
    },
    {
      id: 5,
      title: 'Revenus complmentaires',
      iconComponent: Plus,
      fields: ['revenusComplementaires'],
      optional: true
    }
  ];

  const handleChange = (e) => {
    const { name, value } = e.target;
    
    let processedValue = value;
    if (name === 'salaireBrut' || name === 'sam' || name === 'pensionComplete' || name === 'revenusComplementaires') {
      processedValue = value.replace(/[^0-9.]/g, '');
    }
    
    const newFormData = {
      ...formData,
      [name]: processedValue
    };
    setFormData(newFormData);
    
    if (onDataChange && typeof onDataChange === 'function') {
      onDataChange({
        [name]: processedValue
      });
    }
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }

    // Validation en temps rel
    validateField(name, processedValue);
  };

  const validateField = (fieldName, value) => {
    const newErrors = { ...errors };
    
    switch (fieldName) {
      case 'salaireBrut':
        if (value && (isNaN(value) || parseFloat(value) <= 0)) {
          newErrors.salaireBrut = 'Veuillez saisir un salaire brut valide';
        } else {
          delete newErrors.salaireBrut;
        }
        break;
      case 'age':
        if (value && (isNaN(value) || parseInt(value) < 60)) {
          newErrors.age = 'Vous devez avoir au moins 60 ans';
        } else {
          delete newErrors.age;
        }
        break;
      case 'trimestres':
        if (value && (isNaN(value) || parseInt(value) < 150)) {
          newErrors.trimestres = 'Vous devez avoir au moins 150 trimestres valids';
        } else {
          delete newErrors.trimestres;
        }
        break;
      case 'sam':
        if (value && (isNaN(value) || parseFloat(value) <= 0)) {
          newErrors.sam = 'Veuillez saisir un SAM valide';
        } else {
          delete newErrors.sam;
        }
        break;
    }
    
    setErrors(newErrors);
  };

  const validateStep = (stepNumber) => {
    const step = steps.find(s => s.id === stepNumber);
    if (!step) return false;
    
    const stepErrors = {};
    
    step.fields.forEach(field => {
      if (field === 'age' && (!formData.age || parseInt(formData.age) < 60)) {
        stepErrors.age = 'Vous devez avoir au moins 60 ans';
      }
      if (field === 'salaireBrut' && (!formData.salaireBrut || parseFloat(formData.salaireBrut) <= 0)) {
        stepErrors.salaireBrut = 'Veuillez saisir un salaire brut valide';
      }
      if (field === 'trimestres' && (!formData.trimestres || parseInt(formData.trimestres) < 150)) {
        stepErrors.trimestres = 'Vous devez avoir au moins 150 trimestres valids';
      }
      if (field === 'sam' && (!formData.sam || parseFloat(formData.sam) <= 0)) {
        stepErrors.sam = 'Veuillez saisir votre SAM';
      }
    });
    
    setErrors(prev => ({ ...prev, ...stepErrors }));
    return Object.keys(stepErrors).length === 0;
  };

  const scrollToField = (fieldName) => {
    setTimeout(() => {
      const fieldElement = document.querySelector(`input[name="${fieldName}"], select[name="${fieldName}"]`);
      if (fieldElement) {
        fieldElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
        fieldElement.focus();
      }
    }, 100);
  };

  const nextStep = () => {
    const step = steps.find(s => s.id === currentStep);
    if (!step) return;

    // Valider l'tape actuelle et rcuprer les champs en erreur
    const stepErrors = {};
    
    step.fields.forEach(field => {
      if (field === 'age' && (!formData.age || parseInt(formData.age) < 60)) {
        stepErrors.age = 'Vous devez avoir au moins 60 ans';
      }
      if (field === 'salaireBrut' && (!formData.salaireBrut || parseFloat(formData.salaireBrut) <= 0)) {
        stepErrors.salaireBrut = 'Veuillez saisir un salaire brut valide';
      }
      if (field === 'trimestres' && (!formData.trimestres || parseInt(formData.trimestres) < 150)) {
        stepErrors.trimestres = 'Vous devez avoir au moins 150 trimestres valids';
      }
      if (field === 'sam' && (!formData.sam || parseFloat(formData.sam) <= 0)) {
        stepErrors.sam = 'Veuillez saisir votre SAM';
      }
    });
    
    // Si des erreurs existent, les afficher et faire dfiler vers le premier champ en erreur
    if (Object.keys(stepErrors).length > 0) {
      setErrors(prev => ({ ...prev, ...stepErrors }));
      const firstErrorField = step.fields.find(field => stepErrors[field]);
      if (firstErrorField) {
        scrollToField(firstErrorField);
      }
      return;
    }

    // Si valide, passer  l'tape suivante
    setCompletedSteps(prev => new Set([...prev, currentStep]));
    if (currentStep < steps.length) {
      const nextStepNumber = currentStep + 1;
      setCurrentStep(nextStepNumber);
      
      // Faire dfiler vers le premier champ de la nouvelle tape
      const nextStep = steps.find(s => s.id === nextStepNumber);
      if (nextStep && nextStep.fields.length > 0) {
        scrollToField(nextStep.fields[0]);
      }
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      const prevStepNumber = currentStep - 1;
      setCurrentStep(prevStepNumber);
      
      // Faire dfiler vers le premier champ de l'tape prcdente
      const prevStep = steps.find(s => s.id === prevStepNumber);
      if (prevStep && prevStep.fields.length > 0) {
        scrollToField(prevStep.fields[0]);
      }
    }
  };


  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (validateStep(currentStep) && onSubmit && typeof onSubmit === 'function') {
      onSubmit({
        ...formData,
        salaireBrut: parseFloat(formData.salaireBrut),
        tempsPartiel: parseFloat(formData.tempsPartiel),
        age: parseInt(formData.age),
        trimestres: parseInt(formData.trimestres),
        sam: parseFloat(formData.sam),
        pensionComplete: parseFloat(formData.pensionComplete) || 0,
        revenusComplementaires: parseFloat(formData.revenusComplementaires) || 0
      });
    }
  };

  const calculateHoursPerWeek = (tempsPartiel) => {
    const fullTimeHours = 35; // Heures lgales
    return Math.round((tempsPartiel / 100) * fullTimeHours);
  };

  const estimatePension = () => {
    if (formData.sam && formData.trimestres) {
      const sam = parseFloat(formData.sam);
      const trimestres = parseInt(formData.trimestres);
      const taux = Math.min(trimestres / 166, 1); // Taux maximum  166 trimestres
      return Math.round(sam * taux * 0.25); // 25% du SAM pour estimation
    }
    return null;
  };

  const renderStepContent = () => {
    switch (currentStep) {
      case 1:
        return (
          <div className={styles.stepContent}>
            <InputField
              label="Votre ge"
              icon=""
              type="number"
              name="age"
              value={formData.age}
              onChange={handleChange}
              placeholder="Ex: 62"
              min="60"
              max="70"
              error={errors.age}
              success={formData.age && parseInt(formData.age) >= 60 ? "ge valide pour la retraite progressive" : null}
              tooltipContent="Vous devez avoir au moins 60 ans pour bnficier de la retraite progressive"
            />

            <InputField
              label="Salaire brut mensuel actuel"
              icon=""
              type="text"
              name="salaireBrut"
              value={formData.salaireBrut}
              onChange={handleChange}
              placeholder="Ex: 4400"
              pattern="[0-9]*"
              inputMode="numeric"
              error={errors.salaireBrut}
              success={formData.salaireBrut && parseFloat(formData.salaireBrut) > 0 ? "Salaire brut valide" : null}
              tooltipContent={`Votre salaire BRUT mensuel

C'est le montant avant dduction des
cotisations sociales, indiqu en haut
de votre bulletin de salaire.

Exemple : Si vous gagnez 3000 net,
votre brut est environ 3850`}
              helpText="Il s'agit du salaire AVANT prlvement des cotisations. Vous le trouvez sur votre fiche de paie."
            />
          </div>
        );

      case 2:
        return (
          <div className={styles.stepContent}>
            <InputField
              label="Temps partiel souhait"
              icon=""
              type="range"
              name="tempsPartiel"
              value={formData.tempsPartiel}
              onChange={handleChange}
              min="40"
              max="80"
              step="10"
            />
            
            <InfoBox type="info" className={styles.timeInfo}>
              Vous travaillerez : {calculateHoursPerWeek(formData.tempsPartiel)} heures / semaine
              <br />
              Sur une base de 35h hebdomadaires
            </InfoBox>
            
            <InfoBox type="info" className={styles.popularChoice}>
               La plupart des personnes choisissent 60%
            </InfoBox>
          </div>
        );

      case 3:
        return (
          <div className={styles.stepContent}>
            <InputField
              label="Nombre de trimestres valids"
              icon=""
              type="number"
              name="trimestres"
              value={formData.trimestres}
              onChange={handleChange}
              placeholder="Ex: 160"
              min="150"
              max="172"
              error={errors.trimestres}
              success={formData.trimestres && parseInt(formData.trimestres) >= 150 ? "Nombre valide pour votre gnration" : null}
              tooltipContent="Consultez votre relev de carrire sur lassuranceretraite.fr"
            />
            
            <InfoBox type="info" className={styles.externalLinkBox}>
              <strong>O trouver cette information ?</strong>
              <p> Consultez votre relev de carrire sur lassuranceretraite.fr</p>
              <button
                type="button"
                className={styles.externalLink}
                onClick={() => window.open('https://www.lassuranceretraite.fr', '_blank')}
              >
                Ouvrir le site 
              </button>
            </InfoBox>

            <InputField
              label="SAM - Salaire Annuel Moyen"
              icon=""
              type="text"
              name="sam"
              value={formData.sam}
              onChange={handleChange}
              placeholder="Ex: 45000"
              error={errors.sam}
              showCalculator={true}
              onCalculatorResult={(result) => {
                setFormData(prev => ({ ...prev, sam: result.toString() }));
                if (onDataChange && typeof onDataChange === 'function') {
                  onDataChange({ sam: result.toString() });
                }
              }}
              helpText="Moyenne des 25 meilleures annes de salaire"
            />
            
            {!formData.sam && (
              <InfoBox type="warning" className={styles.calculatorPrompt}>
                 Vous ne connaissez pas votre SAM ?
                <br />
                Utilisez le calculateur automatique ci-dessus 
              </InfoBox>
            )}
          </div>
        );

      case 4:
        const estimatedPension = estimatePension();
        return (
          <div className={styles.stepContent}>
            <InputField
              label="Pension  taux plein mensuelle"
              icon=""
              type="text"
              name="pensionComplete"
              value={formData.pensionComplete}
              onChange={handleChange}
              placeholder="Ex: 1200"
              pattern="[0-9]*"
              inputMode="numeric"
              helpText="Montant NET de votre pension complte si vous ne prenez PAS la retraite progressive"
            />
            
            {estimatedPension && !formData.pensionComplete && (
              <InfoBox type="calculator" className={styles.estimateBox}>
                <strong>Estimation automatique :</strong>
                <p>Environ {estimatedPension}/mois (bas sur votre SAM et trimestres)</p>
                <button
                  type="button"
                  className={styles.useEstimate}
                  onClick={() => {
                    setFormData(prev => ({ ...prev, pensionComplete: estimatedPension.toString() }));
                    if (onDataChange && typeof onDataChange === 'function') {
                      onDataChange({ pensionComplete: estimatedPension.toString() });
                    }
                  }}
                >
                  Utiliser cette estimation
                </button>
              </InfoBox>
            )}
          </div>
        );

      case 5:
        return (
          <div className={styles.stepContent}>
            <InfoBox type="info" className={styles.optionalStep}>
              <strong>Cette tape est facultative</strong>
              <p>Mais elle permet un calcul plus prcis de vos revenus totaux</p>
            </InfoBox>
            
            <InputField
              label="Revenus complmentaires mensuels"
              icon=""
              type="text"
              name="revenusComplementaires"
              value={formData.revenusComplementaires}
              onChange={handleChange}
              placeholder="Ex: 200"
              pattern="[0-9]*"
              inputMode="numeric"
              helpText="Location, freelance, autres pensions..."
            />
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className={styles.container}>
      {/* Premier bloc - En-tte et progression */}
      <div className={styles.headerSection}>
        <div className={styles.header}>
          <h2 className={styles.title}>Mode Avanc</h2>
          <p className={styles.description}>
            Avancez pas  pas dans le formulaire dtaill. Vos donnes dj saisies sont pr-remplies et vous pouvez revenir au mode simplifi  tout moment
          </p>
        </div>

        {/* Barre de progression */}
        <div className={styles.progressContainer}>
        <div className={styles.progressBar}>
          {steps.map((step) => {
            const IconComponent = step.iconComponent;
            return (
              <div
                key={step.id}
                className={`${styles.progressStep} ${
                  currentStep === step.id ? styles.current : ''
                } ${completedSteps.has(step.id) ? styles.completed : ''}`}
              >
                <div className={styles.stepIcon}>
                  {completedSteps.has(step.id) ? (
                    <CheckCircle size={16} />
                  ) : (
                    IconComponent && <IconComponent size={16} />
                  )}
                </div>
                <span className={styles.stepNumber}>{step.id}</span>
              </div>
            );
          })}
        </div>
        <div className={styles.progressText}>
          tape {currentStep}/{steps.length} : {steps[currentStep - 1]?.title || ''}
        </div>
      </div>
      </div>

      {/* Deuxime bloc - Contenu des tapes */}
      <div className={styles.contentSection}>
        {/* Contenu de l'tape */}
        <div className={styles.stepContainer}>
        {renderStepContent()}
      </div>

        {/* Navigation */}
        <div className={styles.navigation}>
        <button
          type="button"
          className={styles.prevButton}
          onClick={prevStep}
          disabled={currentStep === 1}
        >
          <ChevronLeft size={16} />
          Prcdent
        </button>

        {currentStep < steps.length ? (
          <button
            type="button"
            className={styles.nextButton}
            onClick={nextStep}
          >
            Continuer
            <ChevronRight size={16} />
          </button>
        ) : (
          <button
            type="button"
            className={styles.submitButton}
            onClick={handleSubmit}
            disabled={isCalculating}
          >
            {isCalculating ? 'Calcul en cours...' : 'Calculer mes revenus prcis'}
          </button>
        )}
        </div>
      </div>
    </div>
  );
};

export default AvanceFormMultiStep;
